
.header {
    /* position: fixed;
    left: 0;
    top: 0; */
    width: 70%;
    background-color: #151156;
    padding: 1% 15%;
    font-size: 25px;
    text-align: left;
    color: #ccc;
}

.topnav {
    overflow: hidden;
    float: right;
}

.topnav a {
  float: left;
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}

.topnav a:hover {
  color: black;
  color: #ccc;
}

.topnav a.active {
  color: #ccc;
}

.topnav .icon {
  display: none;
}

@media screen and (max-width: 600px) {
  .topnav a:not(:first-child) {display: none;}
  .topnav a.icon {
    float: right;
    display: block;
  }
}

@media screen and (max-width: 600px) {
  .topnav.responsive {position: relative;}
  .topnav.responsive .icon {
    position: absolute;
    right: 0;
    top: 0;
  }
  .topnav.responsive a {
    float: none;
    display: block;
    text-align: left;
  }
}